.Project {
  display: flex;
  flex: 0 1 calc(100% - 90rem);
  flex-direction: column;
  justify-content: center;
  height: 27.5rem;
  width: 10rem;
  margin: 1.5rem 2.5rem 1.5rem 2.5rem;
  border-radius: 5rem;
  transform: scale(1);
  transition: 0.2s ease-in-out;
  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    .ImageContainer {
      width: 100%; // margin: 0 auto;
      display: flex;
      justify-content: center;
      .ImageBorder {
        width: fit-content;
        display: block;
        margin: 0 auto;
        img {
          padding-top: 10px;
          height: 12.5rem;
          object-fit: contain;
          border-radius: 2.5rem;
          background-repeat: no-repeat;
        }
      }
    }
  }
  svg {
    font-size: 3rem;
  }
  span {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
  button {
    transform: scale(1);
    transition: 0.2s ease-in-out;
    text-transform: uppercase;
    padding: 0.75rem 1.5rem;
    border-radius: 2em;
    box-sizing: border-box;
    outline: none;
    border: none;
  }
}

.Project:hover {
  // cursor: pointer;
  transform: scale(1.05);
  transition: 0.2s ease-in-out;

  button:hover {
    transform: scale(1.15);
    transition: 0.2s ease-in-out;
    cursor: pointer;
  }
}

// for Themes
.LightBorder {
  box-shadow: 0 0.1rem 0.5rem black;
  a,
  a:visited {
    text-decoration: none;
    button {
      background-color: #3f51b5;
      color: white;
    }
  }
}

.LightBorder:hover {
  background-color: #3f51b5;
  color: white;
  border: none;
  a {
    text-decoration: none;
    button {
      background-color: white;
      color: black;
    }
  }
}

.DarkBorder {
  box-shadow: 0 0.1rem 0.5rem white;
  a,
  a:visited {
    text-decoration: none;
    button {
      background-color: #dc3545;
      color: black;
    }
  }
}

.DarkBorder:hover {
  background-color: #dc3545;
  color: black;
  border: none;
  a {
    text-decoration: none;
    button {
      background-color: #141e30;
      color: white;
    }
  }
}

// for different screens
@media screen and (min-width: 1701px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100% - 80rem);
    margin: 1.5rem 2.5rem 1.5rem 2.5rem;
  }
}

// for different screens
@media screen and (min-width: 1400px) and (max-width: 1700px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100% - 60rem);
    margin: 1.5rem 2.5rem 1.5rem 2.5rem;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1399px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100% - 50rem);
    margin: 1.5rem 2.5rem 1.5rem 2.5rem;
  }
}

@media screen and (min-width: 576px) and (max-width: 1199px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100vw - 10rem);
    margin: 1.5rem 2.5rem 1.5rem 2.5rem;
  }
}

@media screen and (min-width: 400px) and (max-width: 575px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100vw);
    margin: 1.5rem 2.5rem 1.5rem 2.5rem;
    .ImageContainer {
      .ImageBorder {
        width: fit-content;
        margin: 0 auto;
        img {
          padding: 10px;
          height: 10.5rem;
          width: 80%;
          object-fit: contain;
          background-repeat: no-repeat;
        }
      }
    }
  }
}

@media screen and (min-width: 200px) and (max-width: 399px) {
  .Project {
    display: flex;
    flex: 0 1 calc(100vw);
    margin: 1.5rem 0.5rem 1.5rem 0.5rem;
    .ImageBorder {
      width: fit-content;
      margin: 0 auto;
      img {
        padding-top: 10px;
        height: 10.5rem;
        width: 70%;
        object-fit: contain;
        background-repeat: no-repeat;
      }
    }
  }
}
