.Card {
  height: 7.5rem;
  width: 11.25rem;
  border-radius: 5%;
  transform: scale(1);
  transition: 0.2s ease-in-out;

  p {
    margin: 0;
    padding: 0;
    text-align: center;
  }
  svg {
    padding-top: 1.5rem;
  }
  a {
    padding: 0.25rem;
    transform: scale(1);
    transition: 0.2s ease-in-out;
  }
}
.LightCard {
  a,
  a:visited {
    color: black;
  }
}

.DarkCard {
  a,
  a:visited {
    color: white;
  }
}

@media screen and (min-width: 1025px) {
  .Card:hover {
    transform: scale(1.05);
    transition: 0.2s ease-in-out;
    a:hover {
      cursor: pointer;
      transform: scale(1.15);
      transition: 0.2s ease-in-out;
    }
  }

  .LightCard:hover {
    box-shadow: 0 0.1rem 0.5rem black;
  }

  .DarkCard:hover {
    box-shadow: 0 0.1rem 0.5rem white;
  }
}
